<template>
    <div>
        <aaa></aaa>
        <div id="container-second" class="wrapper">
            <div id="container-second-nav">
                <router-link to="search"><input type="text" placeholder="  搜索歌曲"/></router-link>
                <!--<img src="../assets/images/search.png" alt=""/>-->
            </div>
            <h3 id="container-second-nav-font">巅峰榜</h3>
            <div id="container-second-content">
                <div class="empty-box clearfix"  v-for="(value,key) in songList">
                    <img :src='songList[key].albumpic_big' alt="" @click="gitSong(songList[key])"/>
                    <ul>
                        <li @click="gitSong(songList[key])"><span class="font-left">{{songList[key].songname}}</span><br /><span class="font-right">{{songList[key].singername}}</span></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex'
    import Nav from './Nav.vue'
    import sing from './Sing.vue'
    export default {
        name: 'Second',
        data () {
            return {
                singKey:""
            }
        },
        computed:{
            ...mapGetters([
                'songList'
            ])
        },
        methods:{
            ...mapActions([
                'singsong'
            ]),
            gitSong(key){
                this.singsong(key);
            },
        },
        components:{
            sing:sing,
            aaa:Nav
        }
    }
</script>

<style>

    @import '../assets/css/common.css';
    @import '../assets/css/index.css';
    @import '../assets/css/index2.css';
</style>


