import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Second from '@/components/Second'
import Third from '@/components/Third'
import Four from '@/components/Four'
import Search from '@/components/Search'
import KindSong from '@/components/KindSong'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/main',
      name: 'Hello',
      component: Hello
    },
      {
        path:'/second',
          name:'second',
          component:Second
      },
      {
          path:'/third',
          name:'third',
          component:Third
      },
      {
          path:'/four',
          name:'four',
          component:Four
      },{
          path:'*',
          component:Hello
      },
      {
          path:'/search',
          name:'search',
          component:Search
      },
      {
          path:'/kindsong',
          name:'KindSong',
          component:KindSong
      }
  ]
})



