<template>
    <div id="singNav" v-show="fotterflag">
        <div class="singNav-left">
            <img :src="nowSong.albumpic_big" alt="" @click="openDialog">
        </div>
        <div class="singNav-mid">
            <span>{{nowSong.songname}}</span><br /><span>{{nowSong.singername}}</span>
        </div>
        <div class="singNav-right">
            <i class="iconfontyyy" v-show="pauses" @click="pause">&#xe609;</i>
            <i class="iconfontyyy" v-show="plays" @click="play"  >&#xe602;</i>
            <audio :src="nowSong.url"  autoplay="autoplay" v-run="register('test1')"></audio>
        </div>
    </div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex'
    export default {
        name:'Sing',
        data(){
            return {
                pauses:true,
                plays:false,
                elements : {},
                fotterflag:false,
                dialogFlag:false
            }
        },
        computed:{
            ...mapGetters([
                'nowSong',
            ]),
        },
        directives : {
            run (el, binding) {
                binding.value(el);
            }
        },
        activated(){
            this.fotterflag = true;
        },
        watch:{
            nowSong(val){
                if(val != ""){
                    this.fotterflag = true;
                }
            }
        },
        methods:{
            ...mapActions([
                'openDialog'
            ]),
            pause(){
                this.pauses = !this.pauses;
                this.plays = !this.plays;
                this.elements.test1.pause();
            },
            play(){
                this.pauses = !this.pauses;
                this.plays = !this.plays;
                this.elements.test1.play();
            },
            register (flag) {
                return (el) => {
//                    console.log(el);
                    this.elements[flag] = el;
                };
            },
        },



    }
</script>

<style>
    @import '../assets/css/common.css';
    @import '../assets/css/index.css';
    @import '../assets/css/index2.css';
    @import '//at.alicdn.com/t/font_mxkmobomb8ci8uxr.css';

    #singNav{
        /*width: 100%;*/
        height: 6rem;
        background: #ff0000;
        position:fixed;
        bottom:0;
        z-index: 9999999;
    }
    .singNav-left{
        height: 100%;
        line-height: 6rem;
        float: left;
        margin-left: 2rem;
        margin-right: 2rem;

    }
    .singNav-left img{
        display: inline-block;
        height: 80%;
        border-radius:50%;
        position:absolute;
        top:50%;
        transform: translateY(-50%);
        border:1px solid white;
        box-shadow: 1px 1px 1px #ccc;
    }
    .singNav-mid{
        display: inline-block;
        margin-left: 5rem;
        position:absolute;
        top:50%;
        transform: translateY(-50%);
        color:white;
        font-size:1.6rem;
    }

    .singNav-right{
        float: right;
        margin-right: 4rem;
        height: 6rem;
        line-height: 6rem;

    }
</style>


