<template>
    <div id="diaLog">
        <div id="call-back">
            <span @click="openDialog">&lt</span>
        </div>
        <div id="img-run">
            <!--<transition name="fade">-->
                <div>
                    <img :src="nowSong.albumpic_big" alt="">
                </div><br />
            <!--</transition>-->
            <span>{{nowSong.songname}}</span><br />
            <span>{{nowSong.singername}}</span>
        </div>
    </div>
</template>
<script>
    import {mapGetters,mapActions} from 'vuex'

    export default {

        computed:{
            ...mapGetters([
                'nowSong'
            ])
        },
        methods:{
            ...mapActions([
                'openDialog'
            ])
        }
    }
</script>


<style>
    #diaLog{
        position:fixed;
        top:0;
        left: 0;
        height: 100%;
        width: 100%;
        background: #cccccc;
        z-index: 999999;
    }
    #call-back{
        background: #ff0000;
    }
    #call-back span{
        width:3rem;
        height: 3rem;
        background: #fff;
        /*margin-left:2rem;*/
        display: block;
        font-size:3rem;
        -webkit-border-radius:50%;
        -moz-border-radius:50%;
        border-radius:50%;
        line-height: 3rem;
        text-align: center;
        /*margin-top:2rem;*/
    }
    #img-run{
        margin-top:5rem;
        text-align: center;
    }
    #img-run span{
        display: inline-block;
        margin-top:2rem;
    }
    #img-run div{
        display: inline-block;
        border:1px solid #fff;
        width: 60%;
        height: 60%;
        border-radius:50%;
        box-shadow: 0.2rem 0.2rem 0.2rem rgba(0,0,0,.7);


    }
    #img-run img{
        width: 100%;
        height: 100%;
        border-radius:50%;
        transform-origin: 50% 50%;
        transform:rotate(40deg);
        -webkit-animation:change 1s linear infinite;
    }
    @-webkit-keyframes change

    {
    　　from{
            transform:rotate(0);
    }to{
             transform:rotate(360deg);

         }

    }



</style>


