<template>
    <div id="container" class="wrapper" v-show="songList != ''">
        <aaa></aaa>
        <div class="page-swipe">
            <mt-swipe :auto="4000">
                <mt-swipe-item>
                    <div>
                        <img src="../assets/images/6.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div>
                        <img src="../assets/images/5.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div>
                        <img src="../assets/images/4.jpg" alt="">
                    </div>
                </mt-swipe-item>
                <mt-swipe-item>
                    <div>
                        <img src="../assets/images/3.jpg" alt="">
                    </div>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <div id="classify">
            <router-link to="search">全部歌手</router-link>
            <router-link to="kindsong" href="">歌曲分类</router-link>
        </div>
        <div id="content">
            <h3 id="title">新歌速递</h3>
            <div id="content-imgs">
                <div data-star="" v-for="(value,key) in songList | 3">
                    <img :src="songList[key+10].albumpic_big" alt="" @click="gitSong(songList[key])"/>
                    <span @click="gitSong(songList[key])">{{songList[key+10].songname}}</span>
                </div>
            </div>
        </div>
        <div id="marketing">
            <h3>每日推介</h3>
            <h3>更多</h3>
            <div id="marketing-content">
                <ul>
                    <img :src="songList[1].albumpic_big" @click="gitSong(songList[key])" alt=""/>
                </ul>
                <ul id="marketing-content-font">
                    <li v-for="(value,key) in songList | 3" @click="gitSong(songList[key])">
                        <span class="font-left">{{songList[key].songname}}</span>
                        <span class="font-right">{{songList[key].singername}}</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import Nav from './Nav.vue'
    import Sing from './Sing.vue'
    import {mapGetters,mapActions} from 'vuex'
    export default {
          name: 'hello',
          data () {
            return {
                singKey:''
            }
          },
        computed:{
            ...mapGetters([
                'songList'
            ])
        },
        methods:{
            ...mapActions([
                'singsong'
            ]),
            gitSong(key){
                this.singsong(key);
            },
        },
        components:{
            aaa:Nav,
            sing:Sing
        },

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import '../assets/css/common.css';
    @import '../assets/css/index.css';
    @import '../assets/css/index2.css';

    .page-swipe .mint-swipe {
        width: 100%;
        height:10rem;
        color: #fff;
        font-size: 30px;
        text-align: center;
        margin-bottom: 20px;
        margin-top:4.5rem;
    }
    .page-swipe .mint-swipe-item {
        line-height: 200px;
    }
    .page-swipe img{
        width: 100%;
    }


</style>



