<template>
  <div id="app">
    <router-view></router-view>
    <sing></sing>
    <singrun v-show="dialogFlag"></singrun>

  </div>
</template>
<script>
    import  jquery from '../node_modules/jquery/dist/jquery.min'
    import {mapGetters,mapActions} from 'vuex'
    import Sing from './components/Sing.vue'
    import singrun from './components/Singrun.vue'

    //    import { mapState } from 'vuex'

export default {
      name: 'app',
      data(){
          return {
//              dialogFlag:false
          }
      },
        mounted(){
          this.initData();
        },
        computed:{
            ...mapGetters([
                'songList','songstate','dialogFlag'
            ])
        },
        watch:{
            songstate(val){
                this.initData();
            }
        },
        methods:{
            ...mapActions([
                'initData'
            ])
        },
    components:{
            sing:Sing,
        singrun : singrun
    }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  /*margin-top: 60px;*/
  width: 768px;
  height: 100%;
  overflow:scroll;
  background:rgba(0,0,0,.1);
  margin:0 auto;
  position:relative;
}
</style>



