import vue from 'vue'
import  vuex from 'vuex'
import  jquery from '../node_modules/jquery/dist/jquery.min'

vue.use(vuex);

const state ={
    topid:5,
    songList:'123',
    nowSong:'',
    dialogFlag:false,
    topKind:[
        {
            topid:3,
            toptitle:'欧美',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/H/F/001hQJQ81B2yHF.jpg'
        },
        {
            topid:4,
            toptitle:'流行',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/C/V/004UDoFh359kCV.jpg'

        },
        {
            topid:5,
            toptitle:'内地',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/Z/H/004c33zV1r6cZH.jpg'
        },
        {
            topid:6,
            toptitle:'港台',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/B/M/002G75Ap0SJEBM.jpg'
        },
        {
            topid:16,
            toptitle:'韩国',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/F/M/001H1okO0ISsFM.jpg'
        },
        {
            topid:17,
            toptitle:'日本',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/m/A/002J5MWG3SA9mA.jpg'
        },
        {
            topid:26,
            toptitle:'热歌',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/A/J/000mbhaG00NiAJ.jpg'
        },
        {
            topid:27,
            toptitle:'新歌',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/t/Y/004VHmmP47s4tY.jpg'
        },
        {
            topid:28,
            toptitle:'网络歌曲',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/A/p/000fYG6G142RAp.jpg'
        },
        {
            topid:32,
            toptitle:'音乐人',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/Z/b/002wHbhy2EC1Zb.jpg'
        },
        {
            topid:36,
            toptitle:'K各金曲',
            url:'http://i.gtimg.cn/music/photo/mid_album_300/K/X/003wYYY91FstKX.jpg'
        }


    ]
    // songPlaying:''
};

const mutations = {
    initData(state){
        state.songList = state.songList;
    },
    singsong(state,key){
        state.nowSong = key;
    },
    changeSongKind(state,topid){
        state.topid = topid;
    },
    openDialog(state){
        state.dialogFlag = !state.dialogFlag;
    }
};

const actions = {
    initData:({commit,state}) => {
        new Promise((resolve) => {
            function formatterDateTime() {
                var date=new Date()
                var month=date.getMonth() + 1
                var datetime = date.getFullYear()
                    + ""// "年"
                    + (month >= 10 ? month : "0"+ month)
                    + ""// "月"
                    + (date.getDate() < 10 ? "0" + date.getDate() : date
                        .getDate())
                    + ""
                    + (date.getHours() < 10 ? "0" + date.getHours() : date
                        .getHours())
                    + ""
                    + (date.getMinutes() < 10 ? "0" + date.getMinutes() : date
                        .getMinutes())
                    + ""
                    + (date.getSeconds() < 10 ? "0" + date.getSeconds() : date
                        .getSeconds());
                return datetime;
            }
            jquery.ajax({
                type: 'post',
                url: 'https://route.showapi.com/213-4?topid='+ state.topid,
                dataType: 'jsonp',
                data: {
                    "showapi_timestamp": formatterDateTime(),
                    "showapi_appid": '33523', //这里需要改成自己的appid
                    "showapi_sign": 'b29e0e010c654356b739c130f84c3771'  //这里需要改成自己的应用的密钥secret，

                },
                jsonp: 'jsonpcallback', //这个方法名很重要,不能改变
                error: function(XmlHttpRequest, textStatus, errorThrown) {
                    alert("操作失败!");
                },
                success: function(result) {
                    state.songList = result.showapi_res_body.pagebean.songlist;
                }
            });
            resolve();
            commit('initData');
        });
    },
    // saveSong:({commit}) => {
    //     commit('saveSong');
    // }
    singsong:({commit},key) => {
        commit('singsong',key);
    },
    changeSongKind:({commit},topid) => {
        commit('changeSongKind',topid);
    },
    openDialog:({commit}) => {
        commit('openDialog');
    }

};

const getters = {
    songList:(state) => {
        return state.songList
    },
    nowSong:(state) => {
        return state.nowSong;
    },
    songKind:(state) => {
        return state.topKind;
    },
    songstate:() => {
        return state.topid;
    },
    dialogFlag:() => {
        return state.dialogFlag;
    }
};



export  default new vuex.Store({
    state,
    actions,
    mutations,
    getters,
})


