<template>
    <div>

        <div id="container-third" class="wrapper">
            <aaa></aaa>
            <div class="page-swipe">
                <mt-swipe :auto="4000">
                    <mt-swipe-item>
                        <div>
                            <img src="../assets/images/6.jpg" alt="">
                        </div>
                    </mt-swipe-item>
                    <mt-swipe-item>
                        <div>
                            <img src="../assets/images/5.jpg" alt="">
                        </div>
                    </mt-swipe-item>
                    <mt-swipe-item>
                        <div>
                            <img src="../assets/images/4.jpg" alt="">
                        </div>
                    </mt-swipe-item>
                    <mt-swipe-item>
                        <div>
                            <img src="../assets/images/3.jpg" alt="">
                        </div>
                    </mt-swipe-item>
                </mt-swipe>
            </div>
            <div id="container-third-nav">
                <router-link to="search"><input type="text" placeholder="  搜索歌曲"/></router-link>
                <!--<img src="../assets/images/search.png" alt=""/>-->
            </div>


            <h3 id="container-third-font">全部歌曲</h3>
            <ul id="container-third-content">
                <li v-for="(value,key) in songList" @click="gitSong(songList[key])"><img :src="songList[key].albumpic_big" alt=""/><span>{{songList[key].songname}}</span></li>
            </ul>
        </div>
    </div>

</template>

<script>
    import sing from './Sing.vue'
    import Nav from './Nav.vue'
    import {mapGetters,mapActions} from  'vuex'
    export default {
        name: 'hello',
        data () {
            return {
                singKey:""
            }
        },
        computed:{
            ...mapGetters([
                'songList'
            ])
        },
        methods:{
            ...mapActions([
                'singsong'
            ]),
            gitSong(key){
                this.singsong(key);
            },
        },
        components:{
            sing:sing,
            aaa:Nav
        }
    }
</script>

<style>

    @import '../assets/css/common.css';
    @import '../assets/css/index.css';
    @import '../assets/css/index2.css';

    .page-swipe .mint-swipe {
        width: 100%;
        height:10rem;
        color: #fff;
        font-size: 30px;
        text-align: center;
        margin-bottom: 20px;
        margin-top:4.5rem;
    }
    .page-swipe .mint-swipe-item {
        line-height: 200px;
    }
    .page-swipe img{
        width: 100%;
    }

</style>


