<template>
    <div>
        <mt-search v-model="value" cancel-text="取消">
            <mt-cell
                    v-for="item in song"
                    @click.native= "gitSong(item)"
                    :title="item.songname"
                    :value="item.singername"
                    >
            </mt-cell>
        </mt-search>
    </div>


</template>
<script>
    import vue from 'vue'
    import Sing from './Sing.vue'
    import {mapGetters,mapActions} from 'vuex';
    export default {
        data(){
            return {
                value:'',
                song:[]
            }
        },
        computed:{
            ...mapGetters([
                'songList'
            ])
        },
        methods:{
            ...mapActions([
                'singsong'
            ]),
            gitSong(key){
                console.log(key);
                this.singsong(key);
            },
        },
        watch:{
            value(val){
                this.song = [];
                for(var i=0; i<this.songList.length; i++){
                    if(this.songList[i].songname.indexOf(val) != -1 || this.songList[i].singername.indexOf(val) != -1){
                        this.song.push(this.songList[i]);
                    }
                }
            }
        },
        components:{
            sing:Sing
        }
    }

</script>
<style>

</style>


