<template>
    <div>
        <div id="container-third" class="wrapper">
            <h3 id="container-third-font"><span>歌曲分类</span><span id="btn-back"><router-link to="/">返回</router-link></span></h3>
            <h3 id="sonkind">请选择歌曲类型</h3>
            <ul id="container-third-content">
                <li v-for="(value,key) in songKind" @click="changeSongState(value.topid,key)"  ref="selectImg"><img :src="value.url" alt=""/><span>{{value.toptitle}}</span></li>
            </ul>
        </div>
    </div>
</template>

<script>
    import sing from './Sing.vue'
    import Nav from './Nav.vue'
    import {mapGetters,mapActions} from  'vuex'
    export default {
        name: 'hello',
        data () {
            return {
                singKey:""
            }
        },
        mounted(){
            this.$refs.selectImg[0].className = 'select';
        },
        computed:{
            ...mapGetters([
                'songList','songKind'
            ])
        },
        methods:{
            ...mapActions([
                'singsong','changeSongKind'
            ]),
            gitSong(key){
                this.singsong(key);
            },
            changeSongState(topid,key){
                for(var i=0; i< this.$refs.selectImg.length; i++){
                    this.$refs.selectImg[i].className = '';
                    this.$refs.selectImg[key].className = 'select';
                }

                this.changeSongKind(topid);
            }
        },
        components:{
            sing:sing,
            aaa:Nav
        }
    }
</script>

<style>

    @import '../assets/css/common.css';
    @import '../assets/css/index.css';
    @import '../assets/css/index2.css';

    .page-swipe .mint-swipe {
        width: 100%;
        height:10rem;
        color: #fff;
        font-size: 30px;
        text-align: center;
        margin-bottom: 20px;
        margin-top:4.5rem;
    }
    .page-swipe .mint-swipe-item {
        line-height: 200px;
    }
    .page-swipe img{
        width: 100%;
    }
    #btn-back{
        float:right;
        margin-right:1rem;
    }
    .select{
        color:#f00;
        /*opacity: .5;*/
    }
    #sonkind{
        text-align: center;
        margin-top:2rem;
        margin-bottom:3rem;
        color:#f00;
        font-size:2rem;
    }

</style>


